@font-face {
	font-family: "FontAwesome";
	font-style: normal;
	font-weight: 300;
	font-display: swap;
	src: url("./fonts/fa-light-300.woff2") format("woff2"),
		url("./fonts/fa-light-300.otf") format("opentype");
}

%font-awesome-base {
	// font-family: 'Font Awesome 6 Pro';
	font-family: "FontAwesome";
	font-style: normal;
	font-variant: normal;
	font-display: swap;
	text-rendering: auto;
	-webkit-font-smoothing: antialiased;
}

%font-awesome-light {
	@extend %font-awesome-base;
	font-weight: 300;
}

.fa,
.fas,
.far,
.fal,
.fad,
.fab {
	-moz-osx-font-smoothing: grayscale;
	-webkit-font-smoothing: antialiased;
	// display: inline-block;
	display: block;
	font-style: normal;
	font-variant: normal;
	text-rendering: auto;
	line-height: 1;
}

.fal {
	@extend %font-awesome-light;
}

